import React from 'react';
import Link from 'gatsby-link';

const SecondPage = () => React.createElement(
  'div',
  null,
  React.createElement(
    'h1',
    null,
    'Hi from the second page'
  ),
  React.createElement(
    'p',
    null,
    'Welcome to page 2'
  ),
  React.createElement(
    Link,
    { to: '/' },
    'Go back to the homepage'
  )
);

export default SecondPage;


